import request from '@/utils/request'

/*  */
export const getRemoteList = data => request({ url: '/ygjc/image/list', method: 'post', data })
/*  */
export const addRemoteTask = data => request({ url: '/ygjc/image/add', method: 'post', data })
/*  */
export const saveInterpretationResult = data => request({ url: '/ygjc/iterpretation/add', method: 'post', data })
